
.choose-company {
    padding: 20px;
    padding-bottom: 120px;
    background-color: #303030;
    color: white;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: left;
  }

  .title {
    font-size: 35px;
    font-family: 'Fira Sans', sans-serif;
    margin: 0;
    text-align: left;
  }

  .subtitle {
    font-size: 12px;
    color: white;
    font-family: 'Commisioner', sans-serif;
    text-align: left;
  }

  .search-section {
    width: 100%;
    max-width: 400px;
    margin-bottom: 30px;
  }

  .search-section h3 {
    font-family: 'Fira Sans', sans-serif;
    font-size: 12px;
    margin: 10px 0;
  }

  .search-bar {
    display: flex;
    align-items: center;
    position: relative;
    margin-bottom: 20px;
    border-radius: 10px;
  }

  .search-icon {
    position: absolute;
    left: 15px;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    pointer-events: none;
  }

  .search-icon img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    filter: brightness(100%);
  }

  .search-bar input {
    flex: 1;
    padding: 10px 10px 10px 45px;
    border: none;
    font-size: 12px;
    font-family: 'Commisioner', sans-serif;
    color: #303030;
    width: 290px;
    height: 30px;
    border-radius: 10px 10px 10px 10px;
    background-color: #fff;
  }

  .search-bar input::placeholder {
    color: #303030;
    font-family: 'Commisioner', sans-serif;
    font-size: 12px;
  }
.loading {
  text-align: center;
  color: #fff;
  margin-top: 10px;
  font-family: 'Commisioner', sans-serif;
  font-size: 12px;
}

.search-results {
  background-color: #fff;
  border-radius: 10px;
  margin-top: 10px;
  padding: 10px;
  max-height: 200px;
  overflow-y: auto;
}

.search-result-item {
  display: flex;
  align-items: center;
  padding: 8px 0;
  border-bottom: 1px solid #e0e0e0;
  cursor: pointer;
  transition: background-color 0.2s;
}

.search-result-item:hover {
  background-color: #f0f0f0;
}

.search-result-item:last-child {
  border-bottom: none;
}

.result-icon {
  width: 40px;
  height: 40px;
  margin-right: 10px;
  border-radius: 8px;
  object-fit: cover;
}

.result-text {
  flex: 1;
}

.result-name {
  font-family: 'Fira Sans', sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
}

.result-description {
  font-family: 'Commisioner', sans-serif;
  font-size: 12px;
  color: #666;
  margin: 2px 0 0 0;
}

.filter-btn {
    border: none;
    border-radius: 20px;
    width: 40px;
    height: 40px;
    cursor: pointer;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #B4F87C;
    margin-left: 20px;
  }

  .filter-btn img {
    width: 50%;
    height: 50%;
    object-fit: cover;
  }

  .search-bar:after {
    content: '';
    position: absolute;
    bottom: -125px;
    left: 3px;
    margin-left: 0;
    width: 405px;
    height: 1px;
    background-color: #ffffff;
  }

  .invite-input {
    padding: 10px;
    border: none;
    font-size: 12px;
    font-family: 'Commisioner', sans-serif;
    color: #303030;
    width: 390px;
    height: 30px;
    border-radius: 10px;
    margin-bottom: 50px;
  }

  .create-btn {
    width: 408px;
    align-self: center;
    padding: 10px;
    background-color: transparent;
    border: 1px solid white;
    border-radius: 5px;
    color: #303030;
    background-color: #F9F9F9;
    cursor: pointer;
    margin-left: 3px;
    border-radius: 20px;
    font-family: 'Fira Sans', sans-serif;
    font-size: 12px;
  }

  .popular-section {
    margin-top: -40px;
    width: 100%;
    max-width: 400px;
  }

  .popular-section h2 {
    font-family: 'Fira Sans', sans-serif;
    font-size: 20px;
    margin-bottom: 20px;
  }

  .organization-grid {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: auto auto;
    gap: 0px;
  }

  .organization-card {
    position: relative;
    border-radius: 20px;
    overflow: hidden;
    justify-self: center;
    border: none;
    background: none;
    padding: 0;
    cursor: pointer;
    transition: transform 0.2s ease, opacity 0.2s ease, border 0.2s ease;
    font-family: 'Fira Sans', sans-serif;
    font-size: 12px;
  }

  .organization-card:hover {
    transform: scale(1.05);
    opacity: 0.9;
  }

  .organization-card:active {
    transform: scale(0.95);
  }

  .organization-card.selected {
    border: 2px solid #b4f87c;
  }

  .organization-card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }

  .organization-name {
    position: absolute;
    top: 10px;
    left: 10px;
    margin: 0;
    font-size: 0.9rem;
    color: white;
    padding: 5px 10px;
    border-radius: 5px;
    z-index: 1;
    line-height: 1.2;
    white-space: normal;
    word-wrap: break-word;
    max-width: 80%;
    margin-top: 90px;
    text-align: left;
  }

  .organization-card.first .organization-name {
    margin-top: 83px;
    left: 20px;
  }

  .organization-card.second .organization-name {
    margin-top: 70px;
    left: 10px;
  }

  .organization-card.third .organization-name {
    bottom: -4px;
    left: 10px;
    top: auto;
  }

  .organization-card.forth .organization-name {
    margin-top: 70px;
    right: 10px;
    left: auto;
  }

  .organization-card.fifth .organization-name {
    margin-top: 70px;
    left: 10px;
    bottom: auto;
  }

  .organization-card.six .organization-name {
    top: -10px;
    right: 60px;
    left: auto;
  }

  .organization-card.first {
    width: 101px;
    height: 120px;
    margin-left: -130px;
    margin-bottom: 10px;
  }

  .organization-card.second {
    width: 227px;
    height: 120px;
    margin-left: -150px;
  }

  .organization-card.third {
    width: 221px;
    height: 120px;
    margin-left: -10px;
  }

  .organization-card.forth {
    width: 101px;
    height: 120px;
    margin-left: -25px;
    margin-bottom: 10px;
  }

  .organization-card.fifth {
    width: 109px;
    height: 120px;
    margin-left: -130px;
  }

  .organization-card.six {
    width: 221px;
    height: 120px;
    margin-left: -150px;
  }

  .secondary-btn {
    width: 408px;
    height: 32px;
    padding: 5px;
    background-color: #F9F9F9;
    border: 1px solid white;
    border-radius: 20px;
    color: #303030;
    font-family: 'Fira Sans', sans-serif;
    font-size: 15px;
    cursor: pointer;
  }

  .primary-btn {
    width: 408px;
    height: 40px;
    padding: 10px;
    background-color: #B4F87C;
    border: none;
    border-radius: 20px;
    color: black;
    font-family: 'Fira Sans', sans-serif;
    font-size: 18px;
    cursor: pointer;
  }

